import React from "react";
import { Link } from "react-router-dom";

import "../css/cookiespage.css";

const CookiesPage = () => {
	return (
		<div className="cookies-page-wrapper">
			<div className="cookies-page-wrapper__content-wrapper">
				<h1>Figyelj...</h1>
				<p>
					Tudjuk hogy unalmas a <span>STI (COOKIES)</span> tma, de muszj.
				</p>
				<p>
					Az oldal hasznlatval elfogadod azt, hogy mi az oldal mkdtetshez
					stiket hasznlunk. Ha nem szeretnd ezt, krlek hagyd el az oldalt.
				</p>
			</div>
			<Link exact="true" to="/">
				<button title="">rtem!</button>
			</Link>
		</div>
	);
};

export default CookiesPage;
