import React, { Component } from "react";
import { Spring } from "react-spring/renderprops";

import "../../css/cookie.css";
import "../../css/hover.css";

export default class CookiePopup extends Component {
	render() {
		const hideCookie = this.props.hideCookie;
		return (
			<Spring from={{ opacity: 0 }} to={{ opacity: 1 }} config={{ delay: 500 }}>
				{props => (
					<div style={props}>
						<div className="cookie-back">
							<div className="cookie-popup">
								<div className="cookie-wrapper">
									<h1>
										<span>Sti belltsok. - </span>Unalmas. Fraszt. De
										ktelez.
									</h1>
									<p>
										Az oldal hasznlatval elfogadod azt, hogy mi az oldal
										zemeltetshez Stiket (cookie-kat) hasznlunk.
									</p>
									<button onClick={hideCookie} className="hvr-grow">
										rtem!
									</button>
								</div>
							</div>
						</div>
					</div>
				)}
			</Spring>
		);
	}
}
